import { useState, useRef, useEffect } from 'react';
import CopyButton from './CopyButton';
import SplitButton from './SplitButto';

const TextAreaWithCounter = () => {
  const [text, setText] = useState('');
  const [splitTexts, setSplitTexts] = useState<string[]>([]);
  const textareaRef = useRef<HTMLTextAreaElement>(null);

  useEffect(() => {
    if (textareaRef.current) {
      textareaRef.current.style.height = 'auto';
      textareaRef.current.style.height = `${textareaRef.current.scrollHeight}px`;
    }
  }, [text]);

  const handleCopy = () => {
    if (text) {
      navigator.clipboard.writeText(text);
    }
  };

  const handleSplitText = () => {
    if (!text) return;
    
    const chunks = [];
    for (let i = 0; i < text.length; i += 80) {
      chunks.push(text.slice(i, i + 80));
    }
    setSplitTexts(chunks);
  };

  return (
    <div className="max-w-lg mx-auto p-4 bg-white rounded-lg shadow-md">
      <div className="relative">
        <textarea
          ref={textareaRef}
          value={text}
          onChange={(e) => setText(e.target.value)}
          className="w-full p-3 pr-20 border border-gray-300 rounded-md focus:outline-none focus:ring-2 focus:ring-blue-500 resize-none overflow-hidden"
          rows={1}
          placeholder="Введите текст..."
        />
        <div className="absolute right-2 top-2 flex gap-1">
          <SplitButton 
            onClick={handleSplitText} 
            disabled={!text}
          />
          <CopyButton 
            onClick={handleCopy} 
            disabled={!text}
          />
        </div>
      </div>
      <div className="mt-1 text-sm text-gray-500">
        {text.length} символов
      </div>

      {splitTexts.length > 0 && (
        <div className="mt-4 space-y-2">
          <h3 className="font-medium">Разбитый текст:</h3>
          {splitTexts.map((chunk, index) => (
            <div key={index} className="relative">
              <textarea
                value={chunk}
                readOnly
                className="w-full p-3 border border-gray-300 rounded-md bg-gray-50 resize-none overflow-hidden"
                rows={Math.ceil(chunk.length / 80) || 1}
              />
              <div className="absolute right-2 top-2">
                <CopyButton 
                  onClick={() => navigator.clipboard.writeText(chunk)}
                  disabled={false}
                />
              </div>
              <div className="text-xs text-gray-500 mt-1">
                Блок {index + 1} из {splitTexts.length} ({chunk.length} символов)
              </div>
            </div>
          ))}
        </div>
      )}
    </div>
  );
};

export default TextAreaWithCounter;